/* App.css */

body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: Arial, sans-serif;
}

.container {
  width: 1080px;
  height: 1920px;
  display: flex;
  flex-direction: column;
}

.section {
  width: 1080px;
}

.section1 {
  height: 149px;
  background-color: #125520;
}

.section2 {
  height: 1645px;
  position: relative;
}

.section2 .background-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.section2 .text {
  position: absolute;
  top: 187px;
  left: 50px;
  color: white;
}

.text .small-text {
  font-family: NanumSquare;
  font-size: 48px;
  font-weight: 800;
  line-height: 54.48px;
  text-align: left;
}

.text .large-text {
  font-family: NanumSquare;
  font-size: 100px;
  font-weight: 800;
  line-height: 113.5px;
  text-align: left;
}

.section3 {
  flex-grow: 1;
  background-color: #125520;
}

.order-button {
  display: inline-block;
  margin-top: 50px;
  padding: 20px 40px;
  background-color: #ffc107;
  color: #000;
  text-decoration: none;
  font-size: 24px;
  border-radius: 5px;
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
}

.order-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
}

.order-option {
  margin: 20px;
  padding: 20px 40px;
  font-size: 24px;
  cursor: pointer;
}

.back-button {
  display: inline-block;
  margin-top: 30px;
  padding: 10px 20px;
  background-color: #125520;
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  border-radius: 5px;
}
